@import url(https://fonts.googleapis.com/css?family=Open+Sans);
html, body {
  margin: 0;
  padding:0;
  min-height:100%;
}

html {
  font-size: 15px;
  font-weight: 500;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-color: var(--background-color);
  color: #202124;
  height:100vh;
}

h2 {
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    letter-spacing: 0;
    line-height: 22px;
    font-weight: 600;
    text-rendering: optimizeLegibility;
}

@media (min-width: 50em){
    h2 {
        font-size: 24px;
        line-height: 30px;
    }
}

:root {
    --primary-color: #fff;
    --background-color: #f8f9fa;
    --bright-border-color: #e8eaed;
    --card-border-color: #dadce0;
    --filter-table: hsl(180, 31%, 95%);
    --light-grey: #bdc1c6;
    --links-blue: #1967d2;
    --input-green: #80868b;
    --dark-greyish: hsl(180, 8%, 52%);
    --darker-greyish: #5f6368;
    --very-dark-greyish: hsl(180, 14%, 20%);
    --letter-color-black: #202124;
  }
.search-button {
    height: 60px;
    border:  1px solid var(--card-border-color);
    background: var(--primary-color);
    text-align: center;
    color: var(--darker-greyish);
    cursor: pointer;
    font-size: 20px;
    border-right: 0;
    padding-left: 30px;
}
.search-term {
  width: 100%;
  height: 64px;
  outline: none;
  padding-right: 30px;
  background-color: var(--primary-color);
  border: 1px solid var(--card-border-color);
  display: block;
  height: 56px;
  width: 100%;
  border-left: 0;
  border-right: 0;
}

.search-term:focus{
color:  var(--letter-color-black);
}
.search {
  width: 100%;
  position: relative;
  display: flex;
}
.job-term {
    border: 1px solid var(--card-border-color);
    border-radius: 4px;
    box-shadow: 0 2px 4px rgb(0 0 0 / 11%);
    font-size: 16px;
    height: 48px;
    line-height: 20px;
    padding: 14px 15px;

    -webkit-appearance: none;
    height: 35px;
    width: 100%;


    -webkit-appearance: textfield;
    outline-offset: -2px;
}
.accordion {
    border-bottom: 1px solid var(--bright-border-color);
    padding: 20px 0;
}

.accordion-label{
    display: flex;
    justify-content: space-between;
    color: var(--letter-color-black);
    font-weight: bold;
    position: relative;
    text-transform: capitalize;
    cursor: pointer;
}

.accordion-content{
    background: var(--primary-color);
    line-height: 1.6;
    font-size: 0.85em;
    display: none;

    margin-top: 18px;
}

.accordion-input{
    display: none;
}


.accordion-input:checked ~ .accordion-content{
    display: block;
}
.checkbox-input {
    display: inline-block;
    fill: var(--input-green);
    flex-shrink: 0;
    height: 20px;
    margin-right: 10px;
    -webkit-transform: translateY(-1px);
        transform: translateY(-1px);
    width: 20px;
}

.checkbox {
    display: flex;
    position: relative;
    align-items: center;
    line-height: 20px;
}
.options-list {
    list-style-type: none;
    margin: 0;
    padding-left: 0;
    display: flex;
    flex-flow: row wrap;
}

.list-item {
    width: 50%;
    margin-bottom: 6px;
}
.side-nav {
    position: fixed;
    left: -200%;
    top: 0;
    flex-direction: column;
    background-color: var(--primary-color);
    text-align: center;
    transition: 0.3s;
    z-index: 2;

    padding: 15px;
}

.side-nav.active {
    display: flex;
    align-items: flex-end;
    left: 0;
}

.hamburger {
    display: block;
    cursor: pointer;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background-color: var(--letter-color-black);
}

.hamburger.active .bar:nth-child(2) {
    opacity: 0;
}

.hamburger.active .bar:nth-child(1) {
    -webkit-transform: translateY(8px) rotate(45deg);
            transform: translateY(8px) rotate(45deg);
}

.hamburger.active .bar:nth-child(3) {
    -webkit-transform: translateY(-8px) rotate(-45deg);
            transform: translateY(-8px) rotate(-45deg);
}

.side-nav-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: -200%;
    height: 100%;
    z-index: 1;
    transition: 0.3s;
    background-color: var(--primary-color);
}

.side-nav-overlay.active{
    right: 0;
    left: 0;
}

.edit-filters {
    display: flex;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.15;
    text-align: center;
    align-self: flex-end;
    cursor: pointer;
    height: 46px;
    min-width: 48px;
    flex-direction: column;
    justify-content: center;
    padding: 0 30px;
    color: var(--links-blue);
}


@media (min-width: 50em) {
    .side-nav  {
        background-color: var(--primary-color);
        padding: 15px;
        display: flex;
        flex-flow: column nowrap;
        flex-shrink: 0;
        width: 372px;
        border-right: 1px solid var(--bright-border-color);
        bottom: 0;
        top: 0;
        left: 0;
        position: relative;
    }

    .edit-filters {
        display: none;
    }

    .hamburger {
        display: none;
    }

    .side-nav-overlay {
        display: none;
    }
}

.side-nav-content {
    display: flex;
    flex-direction: column;
}

.filter-section {
    display: flex;
    padding: 20px 0;
    justify-content: space-between;
    align-items: baseline;
    flex-wrap: wrap;
}

.filter-section:first-child {
    padding-top: 0;
}

.filter-section > a {
    margin-top: 8px;
    cursor: pointer;
    color: var(--links-blue);
    background: transparent;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.15;
    text-align: center;
    text-decoration: none;
}
.title {
    -webkit-box-orient: vertical;
    display: -webkit-box;
    font-size: 18px;
    -webkit-line-clamp: 2;
    line-height: 23px;
    margin-bottom: 9px;
    margin-top: 0;
    overflow: hidden;
}

@media (min-width: 50em){
    .title {
        font-size: 24px;
        line-height: 30px;
    }
}
.icon {
    -ms-grid-row-align: center;
    align-self: center;
    display: block;
    color: var(--light-grey);
    margin-right: 5px;
}

.job-detail {
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 14px;
    vertical-align: middle;
}

.job-detail:not(:last-child) {
    margin-right: 10px;
}

@media (min-width: 50em){
    .job-detail:not(:last-child) {
        margin-right: 12px;
    }
}
.job-details {
    display: flex;
}
.card-header {
    color: var(--letter-color-black);
    margin-left: 18px;
    margin-right: 18px;
    padding: 0 18px;
    position: relative;
    width: 100%;
    flex: 1 1
}

@media (min-width: 50em) {
    .card-header {
        border-bottom: 1px solid var(--card-border-color);
        margin-left: 30px;
        margin-right: 30px;
        padding: 18px 0 8px;
    }
}
.description {
    font-size: 14px;
    line-height: 18px;
}

@media (min-width: 50em) {
    .description {
        font-size: 16px;
    }    
}
.skill {
    background-color: var(--light-grey);
    color: var(--background-color);
    font-weight: 700;
    padding: .25rem .5rem;
    border-radius: 5px;
}

.skill:not(:last-child) {
    margin-right: 1rem;
}
.skill-list {

    margin-top: 15px;
}
.job-apply {
    margin-top: 20px;
    cursor: pointer;
    color: var(--links-blue);
    background: transparent;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.15;
    text-align: center;
    text-decoration: none;
}
.card-body  {
    display: flex;
    flex-direction: column;
    color: var(--letter-color-black);
    padding-top: 18px;
}
.card-wrapper {
    background: var(--primary-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    /* max-width: 980px; */

    padding: 1.5rem 2rem;
    margin: 1.3rem 0;
    border-radius: 5px;
    position: relative;
    box-shadow: 0 4px 6px 4px rgb(91 164 164 / 20%);
}

.card-wrapper:first-child {
    margin: 0;
}

@media (min-width: 50em) {
    .card-wrapper {
        border: 1px solid var(--card-border-color);
        border-radius: 3px;
        box-shadow: none;
        font-size: 16px;
        line-height: 28px;
    }

    .card-wrapper:first-child {
        margin: 1.3rem 0;
    }

    .card-wrapper:hover {
        border-color: transparent;
        box-shadow: 0 1px 3px 0 rgb(60 64 67 / 30%), 0 4px 8px 3px rgb(60 64 67 / 15%);
    }
}
.jobs-list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    padding: 10px 30px;
    flex: 1 1;
}

@media (min-width: 50em) {
    .jobs-list {
        padding: 30px;
    }
}

.empty-list {
    display: flex;
    -ms-grid-row-align: center;
    align-self: center;
    flex-direction: column;
    align-items: center;
}
.board-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


@media (min-width: 50em) {    
    .board-body {
        flex-direction: row;
        height: inherit;
    }
}

.banner {
    width: 100%;
    height: 10rem;

    background: url(/static/media/big-header-desktop.8506b8fa.jpg) no-repeat center center;
    -ms-background-size: cover;
    background-size: cover;
}

@media (min-width: 50em) {    
    .banner {
        height: 16rem;
    }
}

